@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

/** {
  outline: 2px solid red;
  outline-offset: -2px;
}*/
.body {
  height: 100%;
  display: flex;
  flex-flow: column;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

.header {
  color: white;
  background: url("../img/printed-circuit-board-desktop.png") no-repeat 85.6% 259px, #1B2D37 url("../img/header-background-desktop.png") no-repeat 0 -59px;
}
@media (min-resolution: 2dppx) {
  .header {
    background: url("../img/printed-circuit-board-desktop.png") no-repeat 85.6% 259px, #1B2D37 url("../img/header-background-desktop@2x.png") no-repeat 0 -59px;
  }
}
@media (max-width: 1023.99px) {
  .header {
    background: url("../img/printed-circuit-board-tablet.png") no-repeat 100% 113px, url("../img/header-background-tablet.png") no-repeat 0 -6px/cover;
  }
}
@media (max-width: 1023.99px) and (min-resolution: 2dppx) {
  .header {
    background: url("../img/printed-circuit-board-tablet@2x.png") no-repeat 100% 113px, url("../img/header-background-tablet@2x.png") no-repeat 0 -6px/cover;
  }
}
@media (max-width: 767.99px) {
  .header {
    background: url("../img/printed-circuit-board-mobile.png") no-repeat 100% 91px, url("../img/header-background-mobile.png") no-repeat 0 0/cover;
  }
}
